<template>
    <div>
        <el-header style="text-align: center; font-size: 12px">
            <el-dropdown>
                <i class="el-icon-setting" style="margin-right: 15px"></i>
            </el-dropdown>
            <span>北京信息科技大学-软件工程研究中心</span>
        </el-header>
        <el-container style="border: 1px solid #eee;height: 100%;">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246);">
                <el-menu :default-openeds="['3']" default-active="3-2">
                    <el-menu-item class="active" index="1" onclick="location.href='/admin/manageStudents'"><i
                            class="el-icon-message"></i>学生管理</el-menu-item>
                    <el-menu-item index="2" onclick="location.href='/'"><i
                            class="el-icon-message"></i>导师管理</el-menu-item>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-setting"></i>科研成果管理</template>
                        <el-menu-item-group>
                            <el-menu-item index="3-1" onclick="location.href='/admin/managePapers'">学术论文</el-menu-item>
                            <el-menu-item index="3-2" onclick="location.href='/admin/managePatents'">技术专利</el-menu-item>
                            <el-menu-item index="3-3" onclick="location.href='/admin/manageCopyrights'">软件著作权</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-container>
                <el-main>
                    <el-button type="primary" @click="addPatent">新增</el-button>
                    <el-table :data="tableData" height="760">
                        <el-table-column type="index" min-width="80" align="center">
                        </el-table-column>
                        <el-table-column prop="author" label="作者" min-width="300" align="center">
                        </el-table-column>
                        <el-table-column prop="title" label="专利名称" min-width="350" align="center">
                        </el-table-column>
                        <el-table-column prop="patentId" label="申请号" min-width="150" align="center">
                        </el-table-column>
                        <el-table-column prop="authorized" label="状态" min-width="100" align="center">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.authorized != 0" type="primary" disable-transitions>已授权</el-tag>
                                <el-tag v-if="scope.row.authorized == 0" type="warning" disable-transitions>未授权</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="published" label="操作" min-width="110" align="center">
                            <template slot-scope="scope">
                                <el-button @click="editPatent(scope.row)" type="text" size="mini">编辑</el-button>
                                <el-button type="danger" size="mini" @click="deletePatent(scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
            </el-container>
        </el-container>
        <el-dialog style="margin: 5%" title="添加专利" :visible.sync="dialogVisible_add" width="50%"
            :before-close="handleClose_add">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="作者" prop="author">
                    <el-input v-model="ruleForm.author" placeholder="请输入所有作者姓名"></el-input>
                </el-form-item>
                <el-form-item label="专利名称" prop="title">
                    <el-input v-model="ruleForm.title"></el-input>
                </el-form-item>
                <el-form-item label="专利号" prop="patentId">
                    <el-input v-model="ruleForm.patentId"></el-input>
                </el-form-item>
                <el-form-item label="是否已授权" prop="authorized">
                    <el-radio-group v-model="ruleForm.authorized">
                        <el-radio label="1">是</el-radio>
                        <el-radio label="0">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_add = false">取 消</el-button>
                    <el-button type="primary" @click="submitAdd('ruleForm')">添加</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog style="margin-top: 5%;" title="编辑专利" :visible.sync="dialogVisible_edit" width="50%"
            :before-close="handleClose">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="作者" prop="author">
                    <el-input v-model="ruleForm.author" placeholder="请输入所有作者姓名"></el-input>
                </el-form-item>
                <el-form-item label="专利名称" prop="title">
                    <el-input v-model="ruleForm.title"></el-input>
                </el-form-item>
                <el-form-item label="专利号" prop="patentId">
                    <el-input v-model="ruleForm.patentId"></el-input>
                </el-form-item>
                <el-form-item label="是否已授权" prop="authorized">
                    <el-radio-group v-model="ruleForm.authorized">
                        <el-radio label="1">是</el-radio>
                        <el-radio label="0">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_edit = false">取 消</el-button>
                    <el-button type="primary" @click="submitEdit('ruleForm')">修改</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>


<script>
import axios from 'axios';

export default {
    name: "managePatents",
    data() {
        return {
            tableData: [],
            dialogVisible_edit: false,
            dialogVisible_add: false,
            ruleForm: {
                id: 0,
                author: '',
                title: '',
                patentId: '',
                authorized: '',
                isDel: 0
            },
            rules: {
                author: [
                    { required: true, message: '请输入所有作者姓名', trigger: 'blur' }
                ],
                title: [
                    { required: true, message: '请输入专利名称', trigger: 'blur' }
                ],
                patentId: [
                    { required: true, message: '请输入专利号', trigger: 'blur' },
                ],
                authorized: [
                    { required: true, message: '请选择授权状态', trigger: 'change' }
                ],

            },
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            axios.get("http://127.0.0.1:5000/patent/findAll").then((res) => {
                console.log(res);
                this.tableData = res.data
            }).catch((err) => {
                console.log(err);
            })
        },
        addPatent() {
            this.ruleForm = {
                id: 0,
                author: '',
                title: '',
                patentId: '',
                authorized: '',
                isDel: 0
            }
            this.dialogVisible_add = true
        },
        editPatent(patent) {
            this.ruleForm = patent
            this.dialogVisible_edit = true
        },
        deletePatent(patent) {
            this.$confirm(`${patent.title}`, '是否删除该专利', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                axios.delete(`${process.env.VUE_APP_API_ENDPOINT}/patent/delete/${patent.id}`).then((res) => {
                    if (res.data == "success") {
                        this.$message({
                            message: '删除成功',
                            type: 'success'
                        });
                        this.getData()
                    } else {
                        this.$message.error('删除失败1');
                    }
                }).catch((err) => {
                    console.log("err:", err);

                    this.$message.error('删除失败2');
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });

        },
        handleClose(done) {
            done()
        },
        handleClose_add(done) {
            done()
        },
        submitAdd(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .post(`${process.env.VUE_APP_API_ENDPOINT}/patent/add`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '添加成功',
                                type: 'success'
                            });
                            console.log(res);
                            this.getData()
                            this.dialogVisible_add = false
                        });
                } else {
                    this.$message.error('添加失败');
                    return false;
                }
            });
        },
        submitEdit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .put(`${process.env.VUE_APP_API_ENDPOINT}/patent/update`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '修改成功',
                                type: 'success'
                            });
                        });
                    this.dialogVisible_edit = false
                } else {
                    console.log('修改失败');
                    return false;
                }
            });
        },
    }
}
</script>

<style>
.el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
}

.el-aside {
    color: #333;
}

.el-tooltip__popper {
    font-size: 14px;
    max-width: 30%
}
</style>