<template>
    <div>
        <el-header style="text-align: center; font-size: 12px">
            <el-dropdown>
                <i class="el-icon-setting" style="margin-right: 15px"></i>
            </el-dropdown>
            <span>北京信息科技大学-软件工程研究中心</span>
        </el-header>
        <el-container style="border: 1px solid #eee;height: 100%;">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246);">
                <el-menu :default-openeds="['3']" default-active="3-1">
                    <el-menu-item class="active" index="1" onclick="location.href='/admin/manageStudents'"><i
                            class="el-icon-message"></i>学生管理</el-menu-item>
                    <el-menu-item index="2" onclick="location.href='/'"><i
                            class="el-icon-message"></i>导师管理</el-menu-item>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-setting"></i>科研成果管理</template>
                        <el-menu-item-group>
                            <el-menu-item index="3-1" onclick="location.href='/admin/managePapers'">学术论文</el-menu-item>
                            <el-menu-item index="3-2" onclick="location.href='/admin/managePatents'">技术专利</el-menu-item>
                            <el-menu-item index="3-3" onclick="location.href='/admin/manageCopyrights'">软件著作权</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-container>
                <el-main>
                    <el-button type="primary" @click="addPaper">新增</el-button>
                    <el-table :data="tableData" height="760">
                        <el-table-column type="index" min-width="50">
                        </el-table-column>
                        <el-table-column prop="introduction" label="描述" min-width="450" header-align="center">
                        </el-table-column>
                        <el-table-column prop="type" label="类型" min-width="110" align="center">
                            <template slot-scope="scope">
                                <el-tag :type="scope.row.type != 'Conference' ? 'success' : 'info'"
                                    disable-transitions>{{ scope.row.type }}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="year" label="年份" min-width="100" align="center">
                        </el-table-column>
                        <el-table-column prop="CCF" label="CCF分类" min-width="150" align="center">
                            <template slot-scope="scope">
                                <el-tag :type="scope.row.CCF != 0 ? 'primary' : 'danger'" disable-transitions>{{
                                    CCF_info[scope.row.CCF]
                                    }}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="SCI" label="检索类型" min-width="150" align="center">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.SCI != 0" :type="'primary'" disable-transitions>{{
                                    SCI_info[scope.row.SCI]
                                    }}</el-tag>
                                &nbsp;&nbsp;&nbsp;&nbsp;
                                <el-tag v-if="scope.row.EI != 0" :type="'warning'" disable-transitions>EI</el-tag>
                                <el-tag v-if="scope.row.EI == 0 && scope.row.SCI == 0" :type="'danger'"
                                    disable-transitions>其他</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="published" label="论文状态" min-width="100" align="center">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.published == 1" type="success" effect="plain">
                                    Published
                                </el-tag>
                                <el-tag v-else type="info" effect="plain">
                                    Accepted
                                </el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column prop="published" label="操作" min-width="110" align="center">
                            <template slot-scope="scope">
                                <el-button @click="editPaper(scope.row)" type="text" size="mini">编辑</el-button>
                                <el-button type="danger" size="mini" @click="deletePaper(scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
            </el-container>
        </el-container>
        <el-dialog style="margin-top: -5%;" title="添加论文" :visible.sync="dialogVisible_add" width="50%"
            :before-close="handleClose_add">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="论文类型" prop="type">
                    <el-radio-group v-model="ruleForm.type" size="medium" @change="changeCCF">
                        <el-radio border label="Journal">期刊论文</el-radio>
                        <el-radio border label="Conference">会议论文</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="作者" prop="author">
                    <el-input v-model="ruleForm.author" placeholder="请输入所有作者姓名"></el-input>
                </el-form-item>
                <el-form-item label="年份" prop="year">
                    <el-input v-model="ruleForm.year"></el-input>
                </el-form-item>
                <el-form-item label="介绍" prop="introduction">
                    <el-input type="textarea" v-model="ruleForm.introduction" placeholder="至少包括：作者. 论文标题. 出处"
                        :rows="4"></el-input>
                </el-form-item>
                <el-form-item label="DOI" prop="href">
                    <el-input v-model="ruleForm.href"></el-input>
                </el-form-item>
                <el-form-item label="CCF分类" prop="CCF">
                    <el-select v-model="ruleForm.CCF" placeholder="请选择CCF分类" @change="changeType">
                        <el-option v-if="ruleForm.type == 'Journal'" label="T1、A类中文期刊" value="1"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="A类国际期刊" value="2"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="B类国际期刊" value="3"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="C类国际期刊" value="4"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="A类国际会议" value="5"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="B类国际会议" value="6"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="C类国际会议" value="7"></el-option>
                        <el-option label="其他" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="EI检索" prop="EI">
                    <el-radio-group v-model="ruleForm.EI">
                        <el-radio label="1">是</el-radio>
                        <el-radio label="2">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="SCI分区" prop="SCI">
                    <el-select v-model="ruleForm.SCI" placeholder="请选择SCI分区">
                        <el-option label="SCI 1区" value="1"></el-option>
                        <el-option label="SCI 2区" value="2"></el-option>
                        <el-option label="SCI 3区" value="3"></el-option>
                        <el-option label="SCI 4区" value="4"></el-option>
                        <el-option label="无" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="发表状态" prop="published">
                    <el-radio-group v-model="ruleForm.published" size="medium">
                        <el-radio border label="1">Published</el-radio>
                        <el-radio border label="0">Accepted</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_add = false">取 消</el-button>
                    <el-button type="primary" @click="submitAdd('ruleForm')">添加</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog style="margin-top: -5%;" title="编辑论文" :visible.sync="dialogVisible_edit" width="50%"
            :before-close="handleClose">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="论文类型" prop="type">
                    <el-radio-group v-model="ruleForm.type" size="medium" @change="changeCCF">
                        <el-radio border label="Journal">期刊论文</el-radio>
                        <el-radio border label="Conference">会议论文</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="作者" prop="author">
                    <el-input v-model="ruleForm.author" placeholder="请输入所有作者姓名"></el-input>
                </el-form-item>
                <el-form-item label="年份" prop="year">
                    <el-input v-model="ruleForm.year"></el-input>
                </el-form-item>
                <el-form-item label="介绍" prop="introduction">
                    <el-input type="textarea" v-model="ruleForm.introduction" placeholder="至少包括：作者. 论文标题. 出处"
                        :rows="4"></el-input>
                </el-form-item>
                <el-form-item label="DOI" prop="href">
                    <el-input v-model="ruleForm.href"></el-input>
                </el-form-item>
                <el-form-item label="CCF分类" prop="CCF">
                    <el-select v-model="ruleForm.CCF" placeholder="请选择CCF分类" @change="changeType">
                        <el-option v-if="ruleForm.type == 'Journal'" label="T1、A类中文期刊" value="1"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="A类国际期刊" value="2"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="B类国际期刊" value="3"></el-option>
                        <el-option v-if="ruleForm.type == 'Journal'" label="C类国际期刊" value="4"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="A类国际会议" value="5"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="B类国际会议" value="6"></el-option>
                        <el-option v-if="ruleForm.type == 'Conference'" label="C类国际会议" value="7"></el-option>
                        <el-option label="其他" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="EI检索" prop="EI">
                    <el-radio-group v-model="ruleForm.EI">
                        <el-radio label="1">是</el-radio>
                        <el-radio label="0">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="SCI分区" prop="SCI">
                    <el-select v-model="ruleForm.SCI" placeholder="请选择SCI分区">
                        <el-option label="SCI 1区" value="1"></el-option>
                        <el-option label="SCI 2区" value="2"></el-option>
                        <el-option label="SCI 3区" value="3"></el-option>
                        <el-option label="SCI 4区" value="4"></el-option>
                        <el-option label="无" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="发表状态" prop="published">
                    <el-radio-group v-model="ruleForm.published" size="medium">
                        <el-radio border label="1">Published</el-radio>
                        <el-radio border label="0">Accepted</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_edit = false">取 消</el-button>
                    <el-button type="primary" @click="submitEdit('ruleForm')">修改</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>


<script>
import axios from 'axios';

export default {
    name: "managePapers",
    data() {
        return {
            tableData: [],
            dialogVisible_edit: false,
            dialogVisible_add: false,
            ruleForm: {
                id: 0,
                type: '',
                author: '',
                introduction: '',
                href: '',
                CCF: '',
                SCI: '',
                EI: '',
                year: '',
                published: '',
                isDel: 0
            },
            rules: {
                type: [
                    { required: true, message: '请选择论文类型', trigger: 'change' }
                ],
                author: [
                    { required: true, message: '请输入所有作者姓名', trigger: 'blur' }
                ],
                introduction: [
                    { required: true, message: '请输入所论文介绍', trigger: 'blur' }
                ],
                year: [
                    { required: true, message: '请输入 发表/接收 年份', trigger: 'blur' },
                ],
                CCF: [
                    { required: true, message: '请选择CCF分类', trigger: 'change' }
                ],
                SCI: [
                    { required: true, message: '请选择SCI分区', trigger: 'change' }
                ],
                EI: [
                    { required: true, message: '请选择是否为EI检索', trigger: 'change' }
                ],
                published: [
                    { required: true, message: '请选择发表状态', trigger: 'change' }
                ],

            },
            CCF_info: [
                "其他",
                "T1、A类中文期刊",
                "A类国际期刊",
                "B类国际期刊",
                "C类国际期刊",
                "A类国际会议",
                "B类国际会议",
                "C类国际会议"
            ],
            SCI_info: [
                "非SCI",
                "SCI 一区",
                "SCI 二区",
                "SCI 三区",
                "SCI 四区",
            ],
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            axios.get("http://127.0.0.1:5000/paper/findAll").then((res) => {
                console.log(res);
                this.tableData = res.data
            }).catch((err) => {
                console.log(err);
            })
        },
        addPaper() {
            this.ruleForm = {
                id: 0,
                type: '',
                author: '',
                introduction: '',
                href: '',
                CCF: '0',
                SCI: '',
                EI: '',
                year: '',
                published: '',
                isDel: 0
            }
            this.dialogVisible_add = true
        },
        editPaper(paper) {
            this.ruleForm = paper
            this.dialogVisible_edit = true
            // console.log(this.ruleForm);
        },
        deletePaper(paper) {
            this.$confirm(`${paper.introduction}`, '是否删除该论文', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                axios.delete(`${process.env.VUE_APP_API_ENDPOINT}/paper/delete/${paper.id}`).then((res) => {
                    if (res.data == "success") {
                        this.$message({
                            message: '删除成功',
                            type: 'success'
                        });
                        this.getData()
                    } else {
                        this.$message.error('删除失败1');
                    }
                }).catch((err) => {
                    console.log("err:", err);
                    
                    this.$message.error('删除失败2');
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });

        },
        handleClose(done) {
            done()
        },
        handleClose_add(done) {
            done()
        },
        submitAdd(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .post(`${process.env.VUE_APP_API_ENDPOINT}/paper/add`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '添加成功',
                                type: 'success'
                            });
                            console.log(res);
                            this.getData()
                            this.dialogVisible_add = false
                        });
                } else {
                    this.$message.error('添加失败');
                    return false;
                }
            });
        },
        submitEdit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .put(`${process.env.VUE_APP_API_ENDPOINT}/paper/update`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '修改成功',
                                type: 'success'
                            });
                        });
                    this.dialogVisible_edit = false
                } else {
                    console.log('修改失败');
                    return false;
                }
            });
        },
        changeCCF() {
            if (this.ruleForm.type == "Journal") {
                if (parseInt(this.ruleForm.CCF) > 4) {
                    this.ruleForm.CCF = "0"
                }
            } else {
                if (parseInt(this.ruleForm.CCF) < 5) {
                    this.ruleForm.CCF = "0"
                }
            }
        },
        changeType() {
            console.log("changeType");
        }
    }
}
</script>

<style>
.el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
}

.el-aside {
    color: #333;
}

.el-tooltip__popper {
    font-size: 14px;
    max-width: 30%
}
</style>