<template>
    <div>
        <el-header style="text-align: center; font-size: 12px">
            <el-dropdown>
                <i class="el-icon-setting" style="margin-right: 15px"></i>
            </el-dropdown>
            <span>北京信息科技大学-软件工程研究中心</span>
        </el-header>
        <el-container style="border: 1px solid #eee;height: 100%;">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246);">
                <el-menu :default-openeds="['3']" default-active="3-3">
                    <el-menu-item class="active" index="1" onclick="location.href='/admin/manageStudents'"><i
                            class="el-icon-message"></i>学生管理</el-menu-item>
                    <el-menu-item index="2" onclick="location.href='/'"><i
                            class="el-icon-message"></i>导师管理</el-menu-item>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-setting"></i>科研成果管理</template>
                        <el-menu-item-group>
                            <el-menu-item index="3-1" onclick="location.href='/admin/managePapers'">学术论文</el-menu-item>
                            <el-menu-item index="3-2" onclick="location.href='/admin/managePatents'">技术专利</el-menu-item>
                            <el-menu-item index="3-3" onclick="location.href='/admin/manageCopyrights'">软件著作权</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-container>
                <el-main>
                    <el-button type="primary" @click="addCopyright">新增</el-button>
                    <el-table :data="tableData" height="760">
                        <el-table-column type="index" min-width="150" align="center">
                        </el-table-column>
                        <el-table-column prop="title" label="著作权名称" min-width="400" align="center">
                        </el-table-column>
                        <el-table-column prop="registrationNum" label="登记号" min-width="150" align="center">
                        </el-table-column>
                        <el-table-column prop="published" label="操作" min-width="110" align="center">
                            <template slot-scope="scope">
                                <el-button @click="editCopyright(scope.row)" type="text" size="mini">编辑</el-button>
                                <el-button type="danger" size="mini" @click="deleteCopyright(scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
            </el-container>
        </el-container>
        <el-dialog style="margin: 5%" title="添加软著" :visible.sync="dialogVisible_add" width="50%"
            :before-close="handleClose_add">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="著作权名称" prop="title">
                    <el-input v-model="ruleForm.title"></el-input>
                </el-form-item>
                <el-form-item label="登记号" prop="registrationNum">
                    <el-input v-model="ruleForm.registrationNum"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_add = false">取 消</el-button>
                    <el-button type="primary" @click="submitAdd('ruleForm')">添加</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog style="margin-top: 5%;" title="编辑专利" :visible.sync="dialogVisible_edit" width="50%"
            :before-close="handleClose">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="著作权名称" prop="title">
                    <el-input v-model="ruleForm.title"></el-input>
                </el-form-item>
                <el-form-item label="登记号" prop="registrationNum">
                    <el-input v-model="ruleForm.registrationNum"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_add = false">取 消</el-button>
                    <el-button type="primary" @click="submitEdit('ruleForm')">修改</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>


<script>
import axios from 'axios';

export default {
    name: "manageCopyrights",
    data() {
        return {
            tableData: [],
            dialogVisible_edit: false,
            dialogVisible_add: false,
            ruleForm: {
                id: 0,
                title: '',
                registrationNum: '',
                isDel: 0
            },
            rules: {
                title: [
                    { required: true, message: '请输入著作权名称', trigger: 'blur' }
                ],
                registrationNum: [
                    { required: true, message: '请输入登记号', trigger: 'blur' },
                ],
            },
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            axios.get(`${process.env.VUE_APP_API_ENDPOINT}/copyright/findAll`).then((res) => {
                console.log(res);
                this.tableData = res.data
            }).catch((err) => {
                console.log(err);
            })
        },
        addCopyright() {
            this.ruleForm = {
                id: 0,
                title: '',
                registrationNum: '',
                isDel: 0
            }
            this.dialogVisible_add = true
        },
        editCopyright(copyright) {
            this.ruleForm = copyright
            this.dialogVisible_edit = true
        },
        deleteCopyright(copyright) {
            this.$confirm(`${copyright.title}`, '是否删除该软著', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                axios.delete(`${process.env.VUE_APP_API_ENDPOINT}/copyright/delete/${copyright.id}`).then((res) => {
                    if (res.data == "success") {
                        this.$message({
                            message: '删除成功',
                            type: 'success'
                        });
                        this.getData()
                    } else {
                        this.$message.error('删除失败1');
                    }
                }).catch((err) => {
                    console.log("err:", err);

                    this.$message.error('删除失败2');
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });

        },
        handleClose(done) {
            done()
        },
        handleClose_add(done) {
            done()
        },
        submitAdd(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .post(`${process.env.VUE_APP_API_ENDPOINT}/copyright/add`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '添加成功',
                                type: 'success'
                            });
                            console.log(res);
                            this.getData()
                            this.dialogVisible_add = false
                        });
                } else {
                    this.$message.error('添加失败');
                    return false;
                }
            });
        },
        submitEdit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .put(`${process.env.VUE_APP_API_ENDPOINT}/copyright/update`, this.ruleForm)
                        .then((res) => {
                            this.$message({
                                message: '修改成功',
                                type: 'success'
                            });
                        });
                    this.dialogVisible_edit = false
                } else {
                    console.log('修改失败');
                    return false;
                }
            });
        },
    }
}
</script>

<style>
.el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
}

.el-aside {
    color: #333;
}

.el-tooltip__popper {
    font-size: 14px;
    max-width: 30%
}
</style>