<template>
    <div>
        <el-header style="text-align: center; font-size: 12px">
            <el-dropdown>
                <i class="el-icon-setting" style="margin-right: 15px"></i>
                <!-- <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>查看</el-dropdown-item>
                            <el-dropdown-item>新增</el-dropdown-item>
                            <el-dropdown-item>删除</el-dropdown-item>
                        </el-dropdown-menu> -->
            </el-dropdown>
            <span>北京信息科技大学-软件工程研究中心</span>
        </el-header>
        <el-container style="border: 1px solid #eee;height: 100%;">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246);">
                <el-menu :default-openeds="['3']" default-active="1">
                    <el-menu-item class="active" index="1" onclick="location.href='/admin/manageStudents'"><i class="el-icon-message"></i>学生管理</el-menu-item>
                    <el-menu-item index="2" onclick="location.href='/'"><i class="el-icon-message"></i>导师管理</el-menu-item>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-setting"></i>科研成果管理</template>
                        <el-menu-item-group>
                            <el-menu-item index="3-1" onclick="location.href='/admin/managePapers'">学术论文</el-menu-item>
                            <el-menu-item index="3-2" onclick="location.href='/admin/managePatents'">技术专利</el-menu-item>
                            <el-menu-item index="3-3" onclick="location.href='/admin/manageCopyrights'">软件著作权</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-container>
                <el-main>
                    <el-button type="primary" @click="addStudent">新增</el-button>
                    <el-table :data="tableData" height="760">
                        <el-table-column type="index" min-width="50">
                        </el-table-column>
                        <el-table-column prop="name" label="姓名" width="100" align="center">
                        </el-table-column>
                        <el-table-column prop="name_en" label="姓名全拼" width="120" align="center"
                            :show-overflow-tooltip="true">
                        </el-table-column>
                        <el-table-column prop="year" label="入学年份" width="80" align="center">
                        </el-table-column>
                        <el-table-column prop="direction" label="研究方向" width="150" :show-overflow-tooltip="true"
                            align="center">
                        </el-table-column>
                        <el-table-column prop="email" label="email" width="200" :show-overflow-tooltip="true"
                            align="center">
                        </el-table-column>
                        <el-table-column prop="introduction" label="个人简介" width="250" :show-overflow-tooltip="true"
                            header-align="center">
                        </el-table-column>
                        <el-table-column prop="destination" label="毕业去向" width="150" :show-overflow-tooltip="true"
                            align="center">
                        </el-table-column>
                        <el-table-column prop="photo" label="照片" width="60">
                            <template slot-scope="scope">
                                <el-popover placement="right" trigger="click">
                                    <el-image style="width: 150px;" :src="'http://127.0.0.1:5000' + scope.row.photo" :fit="fit"></el-image>
                                    <el-button slot="reference" type="text">查看</el-button>
                                </el-popover>
                            </template>
                        </el-table-column>
                        <el-table-column prop="opt" label="操作" align="center">
                            <template slot-scope="scope">
                                <el-button @click="editStudent(scope.row)" type="text" size="mini">编辑</el-button>
                                <el-button type="danger" size="mini">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-main>
            </el-container>
        </el-container>
        <el-dialog title="添加学生" :visible.sync="dialogVisible_add" width="50%" :before-close="handleClose_add">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="姓名全拼" prop="name_en">
                    <el-input v-model="ruleForm.name_en" placeholder="例：Zhengwu Wang"></el-input>
                </el-form-item>
                <el-form-item label="入学年份" prop="year">
                    <el-input v-model="ruleForm.year"></el-input>
                </el-form-item>
                <el-form-item label="研究方向" prop="direction">
                    <el-input v-model="ruleForm.direction"></el-input>
                </el-form-item>
                <el-form-item label="电子邮箱" prop="email">
                    <el-input type="email" v-model="ruleForm.email"></el-input>
                </el-form-item>
                <el-form-item label="个人简介" prop="desc">
                    <el-input type="textarea" v-model="ruleForm.introduction"></el-input>
                </el-form-item>
                <el-form-item label="毕业去向" prop="destination">
                    <el-input v-model="ruleForm.destination"></el-input>
                </el-form-item>
                <el-form-item label="照片">
                    <el-upload class="upload-demo" action="http://127.0.0.1:5000/upload" :on-preview="handlePreview"
                        :on-remove="handleRemove" :before-remove="beforeRemove" multiple :limit="1"
                        :on-success="handle_success" :on-exceed="handleExceed" :file-list="fileList" name="file_imgs">
                        <el-button size="small" type="primary">上传照片</el-button>
                        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                    <el-image style="width: 100px;" :src="'http://127.0.0.1:5000' + ruleForm.photo"
                        :preview-src-list="srcList">
                    </el-image>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_add = false">取 消</el-button>
                    <el-button type="primary" @click="submitAdd('ruleForm')">添加</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog title="编辑学生信息" :visible.sync="dialogVisible_edit" width="50%" :before-close="handleClose">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="ruleForm.name"></el-input>
                </el-form-item>
                <el-form-item label="姓名全拼" prop="name">
                    <el-input v-model="ruleForm.name_en"></el-input>
                </el-form-item>
                <el-form-item label="入学年份" prop="year">
                    <el-input v-model="ruleForm.year"></el-input>
                </el-form-item>
                <el-form-item label="研究方向" prop="direction">
                    <el-input v-model="ruleForm.direction"></el-input>
                </el-form-item>
                <el-form-item label="电子邮箱" prop="email">
                    <el-input type="email" v-model="ruleForm.email"></el-input>
                </el-form-item>
                <el-form-item label="个人简介" prop="desc">
                    <el-input type="textarea" v-model="ruleForm.introduction"></el-input>
                </el-form-item>
                <el-form-item label="毕业去向" prop="destination">
                    <el-input v-model="ruleForm.destination"></el-input>
                </el-form-item>
                <el-form-item label="照片">
                    <el-image style="width: 100px;" :src="'http://127.0.0.1:5000' + ruleForm.photo"
                        :preview-src-list="srcList">
                    </el-image>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogVisible_edit = false">取 消</el-button>
                    <el-button type="primary" @click="submitForm('ruleForm')">修改</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>


<script>
import axios from 'axios';

export default {
    name: "manageStudents",
    created() {
        this.getAllStudents()
    },
    data() {
        return {
            tableData: [
                {
                    id: '1',
                    name: '兰文尉',
                    name_en: 'Wenwei Lan',
                    direction: '软件测试及分析技术',
                    email: 'lww_lowry@bistu.edu.cn',
                    destination: '',
                    introduction: 'Wenwei Lan is currently pursuing the master’s degree at the Computer School, Beijing Information Science and Technology University, Beijing, China.His main research interests include intelligent software analysis and testing technology.',
                    year: '2022',
                    photo: '',
                    isDel: ''
                }
            ],
            dialogVisible_edit: false,
            dialogVisible_add: false,
            fileList: [],
            ruleForm: {
                id: '',
                name: '',
                name_en: '',
                direction: '',
                email: '',
                destination: '',
                introduction: '',
                year: '',
                photo: '',
                isDel: ''
            },
            rules: {
                name: [
                    { required: true, message: '请输入学生姓名', trigger: 'blur' },
                    { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
                ],
                name_en: [
                    { required: true, message: '请输入姓名全拼', trigger: 'blur' },
                    { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
                ],
                year: [
                    { required: true, message: '请输入入学年份', trigger: 'blur' },
                    { min: 4, max: 4, message: '长度为4个数字', trigger: 'blur' }
                ],
                direction: [
                    { required: true, message: '请输入研究方向', trigger: 'blur' },
                    { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
                ],
                email: [
                    { type: 'email', required: true, message: '请输入电子邮箱', trigger: 'change' }
                ],
            },
            srcList: [
                'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'
            ]
        }
    },
    methods: {
        getAllStudents() {
            axios.get("http://127.0.0.1:5000/student/findAll").then((res) => {
                console.log(res);
                this.tableData = res.data
                console.log(this.tableData);
            });
        },
        addStudent() {
            this.ruleForm = {
                id: '',
                name: '',
                direction: '',
                state: 1,
                email: '',
                destination: '',
                introduction: '',
                year: '',
                photo: '',
                isDel: ''
            }
            this.dialogVisible_add = true
        },
        editStudent(student) {
            this.dialogVisible_edit = true
            this.ruleForm = student
            this.srcList = ["http://127.0.0.1:5000" + student.photo]
            console.log(this.ruleForm);
        },
        handleClose(done) {
            done()
        },
        handleClose_add(done) {
            done()
        },
        submitAdd(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .post("http://127.0.0.1:5000/student/add", this.ruleForm)
                        .then((res) => {
                            console.log(res);
                            this.getAllStudents()
                            this.dialogVisible_add = false
                            this.$message({
                                message: '添加成功',
                                type: 'success'
                            });
                        });
                } else {
                    console.log('error submit!!');
                    this.$message.error('添加失败');
                    return false;
                }
            });
        },
        submitEdit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$axios
                        .put("http://127.0.0.1:5000/student/update")
                        .then((res) => {
                            // this.articleList = res.data.content;
                            // this.total = res.data.totalElements;
                        });
                    this.dialogVisible_edit = false
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        showPhoto(photo) {
            console.log(photo);
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(`确定移除 ${file.name}？`);
        },
        handle_success(res) {
            console.log(res)
            this.$message.success('图片上传成功')
            this.srcList = ['http://127.0.0.1:5000' + res]
            this.ruleForm.photo = res[0]
        }
    }
}
</script>

<style>
.el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
}

.el-aside {
    color: #333;
}

.el-tooltip__popper {
    font-size: 14px;
    max-width: 30%
}
</style>